
.page-header{
  width: 100%;
  margin: 0;
  background: #f8f8f8;
  border-bottom: 1px solid #DCDFE6;
}
.page-header .page-header-content{
  height: 30px;
  line-height: 30px;
  padding: 2px 15px;
  background: #f2f2f2;
}
.page-header .page-header-content-box{
  text-align: left;
}
.page-header .page-header-content-box .title i {
  margin-right: 5px;
}
.page-header .page-header-body{
  padding: 10px 15px;
  text-align: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  transition: all .5s ;
  -webkit-transition: all .5s ;
  overflow: hidden;
}
.page-header .page-header-body .search-body-item{
  margin: 0 0 5px 5px;
  display: inline-block;
  font-size: 12px;
  color: #606266;
}
.page-header .f-r{
  float: right;
}



